.task {
  width: 100%; }
  .task .container, .task .action-container, .task .time-container, .task .notes {
    padding: 8px; }
  .task .title {
    text-align: left;
    margin-top: 8px;
    margin-bottom: 8px;
    font-size: 16px; }
  .task .new-note-container {
    width: 100%;
    display: grid;
    grid-template-columns: auto auto auto auto; }
    .task .new-note-container .input {
      grid-column: 1 / 4; }
  .task .time-container .time {
    font-size: 16px;
    font-weight: bold; }
    .task .time-container .time__on {
      color: red; }
    .task .time-container .time__off {
      color: rgba(0, 0, 0, 0.5); }
  .task .notes .note {
    text-align: left;
    margin-bottom: 4px;
    margin-top: 4px;
    font-size: 12px;
    color: rgba(0, 0, 0, 0.5);
    -webkit-margin-before: 0;
            margin-block-start: 0;
    -webkit-margin-after: 0;
            margin-block-end: 0; }



